<template>
    <section v-show="contains(champ.name, champ.role)">
        <figure class="block" :style="{backgroundColor: champ.color}">
        </figure>
        <figcaption>
        {{champ.name}}
        </figcaption>
    </section>
</template>

<script>
export default {
    name: "Champion",
    props: {
        champ: Object,
        searchingChampion: String,
        roleChampion: String,
    },
    methods: {
        contains(name, role){
            if (this.roleChampion==='')
                return name.toLowerCase().includes(this.searchingChampion.toLowerCase())
            else 
                return (name.toLowerCase().includes(this.searchingChampion.toLowerCase())) && role.includes(this.roleChampion)
        }
    }
    
};
</script>

<style scoped>
.block {
    width: 64px;
    height: 64px;
    border: 2px solid #222;
}

* {
    color: #222;
}
</style>