<template>
    <div>
        <input type="text" v-model="searchingChampion" placeholder="Champion name"/>
        <div class="container">
            <figure class="champClass" @click="setActive('Bruiser')"  :class="{champClassActive: activeClass==='Bruiser'}">⚔</figure>
            <figure class="champClass" @click="setActive('Tank')" :class="{champClassActive: activeClass==='Tank'}" >&#9960;</figure>
            <figure class="champClass" @click="setActive('Mage')" :class="{champClassActive: activeClass==='Mage'}" >&#9882;</figure>
            <figure class="champClass" @click="setActive('Ranger')" :class="{champClassActive: activeClass==='Ranger'}">&#127993;</figure>
            <Champion
                v-for="champ in championsList"
                :champ="champ"
                :key="champ.id"
                :searchingChampion="searchingChampion"
                :roleChampion="activeClass"
            />
        </div>
    </div>
</template>

<script>
import Champion from "./Champion.vue";
import champions from "../Champions.js";

export default {
    name: "Searcher",
    components: {
        Champion
    },
    methods: {
        generateColor() {
            let letters = "0123456789ABCDEF";
            let result = "#";
            for (var i = 0; i < 6; i++)
                result += letters[Math.floor(Math.random() * 16)];
            return result;
        },
        sortChampions(){
            return this.championsList.sort((a, b) => (a.name > b.name) ? 1 : -1)
        },
        setActive(className){
            if (this.activeClass == className)
                this.activeClass = ""
            else
                this.activeClass=className;
        },
        setColor() {
            this.championsList.forEach(champ => champ.color = this.generateColor());
        }
    },
    created: function () {
        this.sortChampions()
        this.setColor()
    },
    data() {
        return {
            activeClass: "",
            champions: [],
            championsList: champions, 
            searchingChampion: ""
        };
    },
    
};
</script>

